<template>
  <div class="auth-page">
    <!--  -->
    <div class="video-background" :class="{ 'video-visible': isVideoLoaded }">
      <!--  -  -->
      <div class="fallback-background"></div>

      <!--  -->
      <video id="backgroundVideo" ref="backgroundVideo" class="background-video" loop playsinline
        poster="https://picsum.photos/1920/1080" @error="handleVideoError" @play="handleVideoPlay"
        @pause="handleVideoPause" @loadeddata="markVideoLoaded" @loadedmetadata="checkVideoDimensions">
        <source src="https://123.56.203.202/proxy_files?path=D%3A%5CTemp%5Clingxiyinpin2.mp3" type="video/mp4">
      </video>

      <!--  -->
      <div v-if="showPlayPrompt" class="play-prompt" @click="tryPlayVideo"
        :style="{ display: showPlayPrompt ? 'flex' : 'none' }">
        <div class="play-icon"></div>
        <p></p>
      </div>

      <!--  -  -->
      <div class="video-overlay"></div>
    </div>

    <!--  -  -->
    <div class="brand-header">
      <div class="logo">
        <span class="logo-char"></span>
      </div>
      <h1 class="brand-name">Vox</h1>
    </div>

    <div class="auth-container">
      <!-- / -  -->
      <div class="auth-card">
        <div class="auth-tabs">
          <button class="tab-btn" :class="{ active: isLogin }" @click="isLogin = true">
            
          </button>
          <button class="tab-btn" :class="{ active: !isLogin }" @click="isLogin = false">
            
          </button>
        </div>

        <!--  -->
        <form v-if="isLogin" @submit.prevent="handleLogin" class="auth-form">
          <div class="form-group">
            <label></label>
            <input type="text" v-model="loginForm.username" required placeholder="" autocomplete="username">
          </div>
          <div class="form-group">
            <label></label>
            <input type="password" v-model="loginForm.password" required placeholder=""
              autocomplete="current-password">
          </div>
          
          <button type="submit" class="submit-btn"></button>
        </form>

        <!--  -->
        <form v-else @submit.prevent="handleRegister" class="auth-form">
          <div class="form-group">
            <label></label>
            <input type="text" v-model="registerForm.username" required placeholder="" autocomplete="username">
          </div>
          <div class="form-group">
            <label></label>
            <input type="password" v-model="registerForm.password" required placeholder=""
              autocomplete="new-password">
          </div>
          <div class="form-group">
            <label></label>
            <input type="email" v-model="registerForm.email" required placeholder="" autocomplete="email">
          </div>
          <button type="submit" class="submit-btn"></button>
        </form>

        <!--  -->
        <div v-if="loading" class="loading-overlay">
          <div class="spinner"></div>
          <p>{{ loadingText }}</p>
        </div>

        <!--  -->
        <div v-if="message" :class="['message-toast', messageType]">
          {{ message }}
          <button class="close-btn" @click="clearMessage">&times;</button>
        </div>
      </div>

      
    </div>
  </div>
</template>

<script>
import axios from 'axios';

const API_BASE_URL = 'https://123.56.203.202';

const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 30000,
  withCredentials: true
});

export default {
  data() {
    return {
      isLogin: true,
      loading: false,
      loadingText: '',
      message: '',
      messageType: '',
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        email: ''
      },
      videoError: false,
      showPlayPrompt: false,
      hasUserInteracted: false,
      isVideoLoaded: false,
      videoPlayedOnce: false,
      videoDebugInfo: ''
    };
  },
  mounted() {
    // 
    document.addEventListener('click', this.handleUserInteraction);
    document.addEventListener('touchstart', this.handleUserInteraction);

    // 
    this.checkVideoElementExists();

    // 
    setTimeout(() => {
      this.initVideo();
    }, 300);

    // 
    this.playbackCheckInterval = setInterval(() => {
      this.checkPlayPromptVisibility();
    }, 2000);
  },
  beforeUnmount() {
    document.removeEventListener('click', this.handleUserInteraction);
    document.removeEventListener('touchstart', this.handleUserInteraction);
    clearInterval(this.playbackCheckInterval);
  },
  methods: {
    // DOM
    checkVideoElementExists() {
      const video = this.$refs.backgroundVideo;
      if (video) {
        console.log('DOM');
        // 
        this.$nextTick(() => {
          video.style.display = 'none';
          video.offsetHeight;
          video.style.display = 'block';
        });
      } else {
        console.warn('DOM');
        this.showMessage('', 'error');
      }
    },

    // 
    handleUserInteraction() {
      this.hasUserInteracted = true;
      this.checkPlayPromptVisibility();

      if (!this.videoError && this.$refs.backgroundVideo && this.$refs.backgroundVideo.paused) {
        this.tryPlayVideo();
      }
    },

    // 
    initVideo() {
      const video = this.$refs.backgroundVideo;
      if (!video) {
        console.error('');
        return;
      }

      console.log('');
      this.tryPlayVideo();

      // 2
      setTimeout(() => {
        this.checkPlayPromptVisibility();
      }, 2000);
    },

    // 
    checkPlayPromptVisibility() {
      const video = this.$refs.backgroundVideo;

      console.log(':', {
        hasUserInteracted: this.hasUserInteracted,
        videoPaused: video ? video.paused : true,
        videoError: this.videoError,
        isVideoLoaded: this.isVideoLoaded
      });

      //  +  + 
      if (this.hasUserInteracted && !this.videoError) {
        if (video && video.paused) {
          this.showPlayPrompt = true;
        } else if (!this.isVideoLoaded) {
          this.showPlayPrompt = true;
        }
      }
    },

    // 
    tryPlayVideo() {
      const video = this.$refs.backgroundVideo;
      if (!video) return;

      video.play()
        .then(() => {
          console.log('');
          this.videoPlayedOnce = true;
          this.showPlayPrompt = false;
        })
        .catch(error => {
          console.warn(':', error);
          this.videoPlayedOnce = true;
          this.showPlayPrompt = true;
        });
    },

    // 
    markVideoLoaded() {
      this.isVideoLoaded = true;
      console.log('');
    },

    // 
    handleVideoPlay() {
      this.isVideoLoaded = true;
      this.showPlayPrompt = false;
      this.videoPlayedOnce = true;
      console.log('');
    },

    // 
    handleVideoPause() {
      if (this.videoPlayedOnce && this.hasUserInteracted) {
        this.showPlayPrompt = true;
      }
    },

    // 
    checkVideoDimensions() {
      const video = this.$refs.backgroundVideo;
      if (video) {
        console.log(`: ${video.videoWidth}x${video.videoHeight}`);
        if (video.videoWidth < 100 || video.videoHeight < 100) {
          console.warn('');
          
        }
      }
    },

    // 
    handleVideoError(event) {
      console.error(':', event);
      this.videoError = true;
      this.showPlayPrompt = false;
      this.isVideoLoaded = false;

      let errorMsg = '';
      switch (event.target.error.code) {
        case event.target.error.MEDIA_ERR_ABORTED:
          errorMsg = '';
          break;
        case event.target.error.MEDIA_ERR_NETWORK:
          errorMsg = '';
          break;
        case event.target.error.MEDIA_ERR_DECODE:
          errorMsg = '';
          break;
        case event.target.error.MEDIA_ERR_SRC_NOT_SUPPORTED:
          errorMsg = '';
          break;
        default:
          errorMsg = '';
      }
      this.showMessage(errorMsg, 'error');
    },

    // 
    async handleLogin() {
      try {
        this.loading = true;
        this.loadingText = '...';

        const { data } = await api.post('/sqlLogin', {
          username: this.loginForm.username,
          password: this.loginForm.password
        });

        this.loading = false;

        if (data.success) {
          localStorage.setItem('currentUser', JSON.stringify({
            username: data.username,
            id: data.id
          }));
          localStorage.setItem('username', data.username);

          this.showMessage('', 'success');
          setTimeout(() => {
            this.$router.push('/home/message');
          }, 1000);
        } else {
          this.showMessage(data.message || '', 'error');
        }
      } catch (error) {
        this.loading = false;
        console.error('', error);
        this.showMessage('', 'error');
      }
    },

    // 
    async handleRegister() {
      try {
        this.loading = true;
        this.loadingText = '...';

        const { data } = await api.post('/register', {
          username: this.registerForm.username,
          password: this.registerForm.password,
          email: this.registerForm.email
        });

        this.loading = false;

        if (data.success) {
          this.showMessage('', 'success');
          setTimeout(() => {
            this.isLogin = true;
          }, 1000);
        } else {
          this.showMessage(data.message || '', 'error');
        }
      } catch (error) {
        this.loading = false;
        console.error('', error);
        this.showMessage('', 'error');
      }
    },

    showMessage(text, type) {
      this.message = text;
      this.messageType = type;

      setTimeout(() => {
        this.clearMessage();
      }, 3000);
    },

    clearMessage() {
      this.message = '';
      this.messageType = '';
    }
  }
};
</script>

