    // 
    formatDuration(seconds) {
      if (seconds < 60) {
        return `${seconds}s`;
      } else {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${mins}:${secs < 10 ? '0' + secs : secs}`;
      }
    },

    // 
    showStatus(text, type = 'info') {
      this.statusText = text;
      switch (type) {
        case 'success':
          this.statusClass = 'text-xs text-green-600 mt-2 text-center';
          break;
        case 'error':
          this.statusClass = 'text-xs text-red-600 mt-2 text-center';
          break;
        case 'warning':
          this.statusClass = 'text-xs text-yellow-600 mt-2 text-center';
          break;
        default:
          this.statusClass = 'text-xs text-blue-600 mt-2 text-center';
      }

      if (type !== 'error') {
        setTimeout(() => {
          this.statusText = '';
        }, 3000);
      }
    },

    // 
    async convertToValidWav(blob) {
      if (await this.isValidWavFile(blob)) {
        return blob;
      }

      try {
        const arrayBuffer = await new Response(blob).arrayBuffer();
        const audioContext = new (window.AudioContext || window.webkitAudioContext)({
          sampleRate: 16000
        });

        const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
        const wavBuffer = this.convertToWav(audioBuffer);

        return new Blob([wavBuffer], { type: 'audio/wav' });
      } catch (error) {
        console.error(':', error);
        this.showStatus('', 'warning');
        return blob;
      }
    },

    async isValidWavFile(blob) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onloadend = (e) => {
          if (e.target.readyState === FileReader.DONE) {
            const arrayBuffer = e.target.result;
            const uint8Array = new Uint8Array(arrayBuffer);

            if (uint8Array.length >= 4) {
              const riff = String.fromCharCode(uint8Array[0], uint8Array[1], uint8Array[2], uint8Array[3]);
              resolve(riff === 'RIFF');
            } else {
              resolve(false);
            }
          }
        };
        reader.readAsArrayBuffer(blob.slice(0, 4));
      });
    },

    convertToWav(audioBuffer) {
      const numberOfChannels = audioBuffer.numberOfChannels;
      const sampleRate = audioBuffer.sampleRate;
      const format = 1;
      const bitDepth = 16;

      let data = new Float32Array(audioBuffer.length);
      for (let i = 0; i < audioBuffer.length; i++) {
        let sum = 0;
        for (let c = 0; c < numberOfChannels; c++) {
          sum += audioBuffer.getChannelData(c)[i];
        }
        data[i] = sum / numberOfChannels;
      }

      const buffer = new ArrayBuffer(44 + data.length * 2);
      const view = new DataView(buffer);

      this.writeString(view, 0, 'RIFF');
      view.setUint32(4, 36 + data.length * 2, true);
      this.writeString(view, 8, 'WAVE');
      this.writeString(view, 12, 'fmt ');
      view.setUint32(16, 16, true);
      view.setUint16(20, format, true);
      view.setUint16(22, 1, true);
      view.setUint32(24, sampleRate, true);
      view.setUint32(28, sampleRate * 2, true);
      view.setUint16(32, 2, true);
      view.setUint16(34, bitDepth, true);
      this.writeString(view, 36, 'data');
      view.setUint32(40, data.length * 2, true);

      let index = 44;
      const volume = 1;
      for (let i = 0; i < data.length; i++) {
        const sample = Math.max(-1, Math.min(1, data[i] * volume));
        view.setInt16(index, sample < 0 ? sample * 0x8000 : sample * 0x7FFF, true);
        index += 2;
      }

      return buffer;
    },

    writeString(view, offset, string) {
      for (let i = 0; i < string.length; i++) {
        view.setUint8(offset + i, string.charCodeAt(i));
      }
    },

    // 
    async toggleCamera() {
      if (this.isCameraActive) {
        this.stopCamera();
        this.isCameraModalVisible = false;
      } else {
        this.isCameraModalVisible = true;
        await this.startCamera();
      }
    },

    async startCamera() {
      this.isLoadingCamera = true;
      this.cameraError = null;

      try {
        if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
          throw new Error('');
        }

        if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
          throw new Error('HTTPS');
        }

        // 
        this.videoStream = await navigator.mediaDevices.getUserMedia({
          video: {
            width: { ideal: 1280 },
            height: { ideal: 720 }
          }
        });

        const videoElement = this.$refs.videoElement;
        if (videoElement) {
          videoElement.srcObject = this.videoStream;
          this.isCameraActive = true;
          this.showStatus('', 'success');
        }
      } catch (error) {
        console.error(':', error);
        this.cameraError = error.message;

        // 
        if (error.name === 'NotAllowedError') {
          this.cameraError = '';
        } else if (error.name === 'NotFoundError') {
          this.cameraError = '';
        } else if (error.name === 'NotReadableError') {
          this.cameraError = '';
        } else if (error.name === 'NotSupportedError') {
          this.cameraError = '';
        }

        this.showStatus(`: ${this.cameraError}`, 'error');
      } finally {
        this.isLoadingCamera = false;
      }
    },

    stopCamera() {
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
        this.videoStream = null;
      }

      const videoElement = this.$refs.videoElement;
      if (videoElement) {
        videoElement.srcObject = null;
      }

      this.isCameraActive = false;
      this.isAnalyzingContinuously = false;

      if (this.continuousAnalysisTimer) {
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
      }

      if (this.detectionInterval) {
        clearInterval(this.detectionInterval);
        this.detectionInterval = null;
      }

      this.showStatus('', 'info');
    },

    // /api/chat
    async captureImage() {
      if (!this.isCameraActive || this.isProcessingImage) return;

      this.isProcessingImage = true;
      this.showStatus('...', 'info');

      try {
        const videoElement = this.$refs.videoElement;
        if (!videoElement) throw new Error('');

        // 1. base64
        const canvas = document.createElement('canvas');
        canvas.width = videoElement.videoWidth;
        canvas.height = videoElement.videoHeight;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(videoElement, 0, 0, canvas.width, canvas.height);
        const base64Image = canvas.toDataURL('image/jpeg');

        // 2. /api/chat
        const analysisResult = await this.analyzePose(base64Image);
        // analyzePose/api/chat

        // 3. base64URLfetch
        const blob = await (await fetch(base64Image)).blob(); // base64blob
        const imageUrl = URL.createObjectURL(blob);

        // 4. 
        this.addExpressionMessage(imageUrl, analysisResult);

        this.showStatus('', 'success');
      } catch (error) {
        console.error(':', error);
        this.showStatus(`: ${error.message}`, 'error');
      } finally {
        this.isProcessingImage = false;
      }
    },

    // /
    startContinuousAnalysis() {
      if (this.isAnalyzingContinuously) {
        // 
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
        this.isAnalyzingContinuously = false;
        this.showStatus('', 'info');
      } else {
        // 2
        this.showStatus('...', 'info');
        this.isAnalyzingContinuously = true;

        // 
        this.captureImage();

        // 
        this.continuousAnalysisTimer = setInterval(() => {
          if (this.isAnalyzingContinuously) {
            this.captureImage();
          }
        }, 2000);
      }
    },

    // 
    async analyzeExpression(base64Image) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/predict/image`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`: ${response.status} - ${errorText.substring(0, 100)}`);
        }

        const data = await response.json();

        if (data.status === 'success' && data.predictions) {
          return data.predictions;
        } else {
          throw new Error(data.error || '');
        }
      } catch (error) {
        console.error(':', error);
        throw error;
      }
    },

    // 
    async analyzePose(base64Image) {
      try {
        // 1. ID
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            prompt: '',
            image_base64: base64Image
          })
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`: ${response.status} - ${errorText.substring(0, 100)}`);
        }

        const data = await response.json();

        if (data.status === 'success' && data.task_id) {
          // 2. task_id
          return this.startPolling(data.task_id);
        } else {
          throw new Error(data.error || 'ID');
        }
      } catch (error) {
        console.error(':', error);
        throw error;
      }
    },

    //  - 
    async startPolling(taskId, interval = 1000) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/api/chat/result/${taskId}`, {
          method: 'GET',
          headers: this.commonHeaders
        });

        if (!response.ok) {
          throw new Error(`: ${response.status}`);
        }

        const result = await response.json();

        if (result.status === 'processing') {
          console.log(` ${taskId} ...`);
          //  this 
          return new Promise(resolve => {
            setTimeout(() => {
              resolve(this.startPolling(taskId, interval));
            }, interval);
          });
        } else if (result.status === 'success') {
          console.log(` ${taskId} `);
          //  this.msg 
          this.msg.expressionResults = [result.result]; // v-for
          console.log(':', this.msg.expressionResults);

          return {
            status: 'completed',
            taskId,
            result: result.result,
            timestamp: new Date()
          };
        } else {
          throw new Error(`: ${result.result || ''}`);
        }
      } catch (error) {
        console.error(` ${taskId} :`, error);
        throw error;
      }
    },




    // 
    async analyzeComprehensive(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'expression') return;

      this.showStatus('...', 'info');

      try {
        // 
        let expressionsText = msg.expressionResults.map(exp =>
          `${exp.emotion} (${(exp.confidence * 100).toFixed(1)}%)`
        ).join('');

        let prompt = `:
: ${expressionsText}
: ${msg.poseResult.pose} (: ${(msg.poseResult.score * 100).toFixed(1)}%)

`;

        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });

        if (!response.ok) {
          throw new Error(`: ${response.status}`);
        }

        const data = await response.json();

        if (data.status === 'success') {
          // 
          this.messages[index] = {
            ...this.messages[index],
            comprehensiveAnalysis: data.result
          };

          // localStorage
          const existingAnalysis = localStorage.getItem('interview_expression_gesture_analysis') || '';
          const newAnalysis = existingAnalysis ? `${existingAnalysis}\n${data.result}` : data.result;
          localStorage.setItem('interview_expression_gesture_analysis', newAnalysis);

          this.showStatus('', 'success');
        } else {
          throw new Error(data.result || '');
        }
      } catch (error) {
        console.error(':', error);
        this.showStatus(`: ${error.message}`, 'error');
      }
    },

    // 
    async openEvaluationReport() {
      this.isReportVisible = true;
      this.reportLoading = true;
      this.reportError = '';
      this.reportResponse = null; // 

      // 
      setTimeout(() => {
        this.generateReportFromStorage();
      }, 100);
    },

    // localStorage
    async generateReportFromStorage() {
      try {
        this.reportLoading = true;

        // localStorage
        this.firstResult = localStorage.getItem("firstResult") || '';
        this.secondResult = localStorage.getItem("secondResult") || '';
        this.thirdResult = localStorage.getItem("thirdResult") || '';
        this.fourthResult = localStorage.getItem("fourthResult") || '';
        this.fifthResult = localStorage.getItem("fifthResult") || '';

        this.firstQuestion = localStorage.getItem("firstQuestion") || '';
        this.secondQuestion = localStorage.getItem("secondQuestion") || '';
        this.thirdQuestion = localStorage.getItem("thirdQuestion") || '';
        this.fourthQuestion = localStorage.getItem("fourthQuestion") || '';
        this.fifthQuestion = localStorage.getItem("fifthQuestion") || '';

        // 
        if (!this.firstResult && !this.secondResult && !this.thirdResult && !this.fourthResult && !this.fifthResult) {
          throw new Error('');
        }

        // 
        this.calculateReportScores();

        // 
        await this.generateDimensionAnalysis();

        // 
        await this.generateComprehensiveEvaluation();
        // 
        await this.generateKeyIssues();

        // 
        await this.generateFeedbackSuggestions();

        // 
        await this.generateComprehensiveEvaluation();

        // 
        this.reportResponse = {
          indicators: this.reportIndicators,
          values: this.reportValues,
          professionalKnowledgeAnalysis: this.professionalKnowledgeAnalysis,
          skillMatchAnalysis: this.skillMatchAnalysis,
          languageExpressionAnalysis: this.languageExpressionAnalysis,
          logicalThinkingAnalysis: this.logicalThinkingAnalysis,
          innovationAbilityAnalysis: this.innovationAbilityAnalysis,
          comprehensiveEvaluation: this.comprehensiveEvaluation,
          improvementSuggestions: this.improvementSuggestions
        };

        // DOM
        this.$nextTick(() => {
          this.renderRadarChart();
        });

      } catch (error) {
        console.error(':', error);
        this.reportError = error.message;
      } finally {
        this.reportLoading = false;
      }
    },

    // 
    calculateReportScores() {
      //  (0-100)
      let knowledgeScore = 60;
      if (this.firstResult && (this.firstResult.length > 100 ||
        this.firstResult.includes('') ||
        this.firstResult.includes('') ||
        this.firstResult.includes(''))) {
        knowledgeScore = Math.floor(Math.random() * 20) + 70; // 70-90
      } else if (this.firstResult && this.firstResult.length < 50) {
        knowledgeScore = Math.floor(Math.random() * 20) + 40; // 40-60
      }

      // 
      let skillScore = 60;
      if (this.secondResult && (this.secondResult.length > 100 ||
        this.secondResult.includes('') ||
        this.secondResult.includes('') ||
        this.secondResult.includes(''))) {
        skillScore = Math.floor(Math.random() * 20) + 70;
      } else if (this.secondResult && this.secondResult.length < 50) {
        skillScore = Math.floor(Math.random() * 20) + 40;
      }

      // 
      let languageScore = 60;
      if (this.thirdResult && (this.thirdResult.length > 100 ||
        this.thirdResult.includes('') ||
        this.thirdResult.includes('') ||
        this.thirdResult.includes(''))) {
        languageScore = Math.floor(Math.random() * 20) + 70;
      } else if (this.thirdResult && this.thirdResult.length < 50) {
        languageScore = Math.floor(Math.random() * 20) + 40;
      }

      // 
      let logicScore = 60;
      if (this.fourthResult && (this.fourthResult.length > 100 ||
        this.fourthResult.includes('') ||
        this.fourthResult.includes('') ||
        this.fourthResult.includes(''))) {
        logicScore = Math.floor(Math.random() * 20) + 70;
      } else if (this.fourthResult && this.fourthResult.length < 50) {
        logicScore = Math.floor(Math.random() * 20) + 40;
      }

      // 
      let innovationScore = 60;
      if (this.fifthResult && (this.fifthResult.length > 100 ||
        this.fifthResult.includes('') ||
        this.fifthResult.includes('') ||
        this.fifthResult.includes(''))) {
        innovationScore = Math.floor(Math.random() * 20) + 70;
      } else if (this.fifthResult && this.fifthResult.length < 50) {
        innovationScore = Math.floor(Math.random() * 20) + 40;
      }

      this.reportValues = [
        knowledgeScore,
        skillScore,
        languageScore,
        logicScore,
        innovationScore
      ];
    },

    // 
    // 
    async generateDimensionAnalysis() {
      // localStorage
      const firstQuestion = localStorage.getItem("firstQuestion") || '';
      const secondQuestion = localStorage.getItem("secondQuestion") || '';
      const thirdQuestion = localStorage.getItem("thirdQuestion") || '';
      const fourthQuestion = localStorage.getItem("fourthQuestion") || '';
      const fifthQuestion = localStorage.getItem("fifthQuestion") || '';

      //  - 
      if (this.firstResult) {
        const prompt = `
${firstQuestion}
${this.firstResult}

`;
        this.professionalKnowledgeAnalysis = await this.getAnalysisFromAPI(prompt);
      }

      //  - 
      if (this.secondResult) {
        const prompt = `
${secondQuestion}
${this.secondResult}

`;
        this.skillMatchAnalysis = await this.getAnalysisFromAPI(prompt);
      }

      //  - 
      if (this.thirdResult) {
        const prompt = `
${thirdQuestion}
${this.thirdResult}

`;
        this.languageExpressionAnalysis = await this.getAnalysisFromAPI(prompt);
      }

      //  - 
      if (this.fourthResult) {
        const prompt = `
${fourthQuestion}
${this.fourthResult}

`;
        this.logicalThinkingAnalysis = await this.getAnalysisFromAPI(prompt);
      }

      //  - 
      if (this.fifthResult) {
        const prompt = `
${fifthQuestion}
${this.fifthResult}

`;
        this.innovationAbilityAnalysis = await this.getAnalysisFromAPI(prompt);
      }
    },


    // API
    async getAnalysisFromAPI(prompt) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });

        if (!response.ok) {
          throw new Error(`: ${response.status}`);
        }

        const data = await response.json();

        if (data.status === 'success') {
          const taskId = data.task_id;
          const pollInterval = 2000; // 2
          const maxAttempts = 150;   // 1505

          // Promise
          return new Promise((resolve, reject) => {
            let attempts = 0;

            // 
            const pollForFile = () => {
              // 
              if (attempts >= maxAttempts) {
                reject(new Error(''));
                return;
              }

              attempts++;

              // 
              fetch(`${this.API_BASE_URL}/get_task_file_url`, {
                method: 'POST',
                headers: {
                  ...this.commonHeaders,
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify({ task_id: taskId })
              })
                .then(response => {
                  // JSON
                  return response.json().then(data => ({ data, response }));
                })
                .then(({ data, response }) => {
                  // URL
                  if (data.status === 'success' && data.file_url) {
                    // 
                    fetch(data.file_url, {
                      method: 'GET',
                      headers: this.commonHeaders
                    })
                      .then(fileResponse => {
                        if (!fileResponse.ok) {
                          throw new Error(`: ${fileResponse.status}`);
                        }
                        return fileResponse.text();
                      })
                      .then(fileContent => {
                        // "processing"
                        if (fileContent.trim() === 'processing') {
                          // 
                          setTimeout(pollForFile, pollInterval);
                        } else {
                          // 
                          resolve(fileContent);
                        }
                      })
                      .catch(() => {
                        // 
                        setTimeout(pollForFile, pollInterval);
                      });
                  }
                  // 
                  else if (response.status === 404 ||
                    (data.status === 'error' && data.message.includes(''))) {
                    setTimeout(pollForFile, pollInterval);
                  }
                  // 
                  else {
                    reject(new Error(`: ${data.message || ''}`));
                  }
                })
                .catch(error => {
                  // 
                  setTimeout(pollForFile, pollInterval);
                });
            };

            // 
            pollForFile();
          });
        } else {
          return '';
        }
      } catch (error) {
        console.error(':', error);
        return '';
      }
    },

    // 
    async checkRecordingSupport() {
      try {
        // API
        if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
          throw new Error('');
        }

        // 
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });

        // 
        stream.getTracks().forEach(track => track.stop());

        // 
        this.recordingSupported = true;
        console.log('');
      } catch (error) {
        this.recordingSupported = false;
        console.warn(':', error.message);
      }
    },
    // /
    toggleRecording() {
      if (this.isRecording) {
        // 
        this.stopRecording();
      } else {
        // 
        this.startRecording();
      }
    },

    // 
    async generateComprehensiveEvaluation() {
      // 
      // prompt
      const prompt = `
1. : ${this.firstResult || ''}${this.reportValues[0]}
2. : ${this.secondResult || ''}${this.reportValues[1]}
3. : ${this.thirdResult || ''}${this.reportValues[2]}
4. : ${this.fourthResult || ''}${this.reportValues[3]}
5. : ${this.fifthResult || ''}${this.reportValues[4]}



[100]


1. []
2. []
3. []`;

      try {
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });

        if (!response.ok) {
          throw new Error(`: ${response.status}`);
        }

        const data = await response.json();

        if (data.status === 'success' && data.result) {
          const result = data.result;

          // 
          const evalMatch = result.match(/\s*([\s\S]*?)(?=)/);
          this.comprehensiveEvaluation = evalMatch ? evalMatch[1].trim() : '';

          // 
          this.improvementSuggestions = [];
          const suggestionsMatch = result.match(/\s*([\s\S]*)/);
          if (suggestionsMatch && suggestionsMatch[1]) {
            // 
            const suggestions = suggestionsMatch[1]
              .split(/\n\d+\./)
              .filter(item => item.trim());

            suggestions.forEach(suggestion => {
              this.improvementSuggestions.push(suggestion.trim());
            });
          }

          // 3
          while (this.improvementSuggestions.length < 3) {
            this.improvementSuggestions.push('');
          }
        }
      } catch (error) {
        console.error(':', error);
        // 
        this.comprehensiveEvaluation = '';
        this.improvementSuggestions = [
          "",
          "",
          ""
        ];
      }
    },

    // generateComprehensiveEvaluation
    async generateKeyIssues() {
      // 
      // prompt
      const prompt = `:
: ${this.firstResult || ''}
: ${this.secondResult || ''}
: ${this.thirdResult || ''}
: ${this.fourthResult || ''}
: ${this.fifthResult || ''}



[1-2]


1. []
2. []
3. []`;

      try {
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });

        if (!response.ok) throw new Error(`: ${response.status}`);

        const data = await response.json();
        if (data.status === 'success' && data.result) {
          const result = data.result;

          // 
          const issueMatch = result.match(/\s*([\s\S]*?)(?=)/);
          this.keyIssues = issueMatch ? issueMatch[1].trim() : '';

          // 
          this.issueExamples = [];
          const examplesMatch = result.match(/\s*([\s\S]*)/);
          if (examplesMatch && examplesMatch[1]) {
            // 
            const examples = examplesMatch[1].split(/\n\d+\./).filter(item => item.trim());
            examples.forEach(example => {
              this.issueExamples.push(example.trim());
            });
            // 3
            this.issueExamples = this.issueExamples.slice(0, 3);
          }
        }
      } catch (error) {
        console.error(':', error);
        this.keyIssues = '';
      }
    },

    async generateFeedbackSuggestions() {
      // 
      // prompt
      const prompt = `5
: ${this.firstResult || ''}
: ${this.secondResult || ''}
: ${this.thirdResult || ''}
: ${this.fourthResult || ''}
: ${this.fifthResult || ''}

2"-"

- 1
- 2


- 1
- 2


- 1
- 2


- 1
- 2


- 1
- 2`;

      try {
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });

        if (!response.ok) throw new Error(`: ${response.status}`);

        const data = await response.json();
        if (data.status === 'success' && data.result) {
          this.feedbackCategories = [];
          const categories = ['', '', '', '', ''];

          categories.forEach(category => {
            // 
            const regex = new RegExp(`${category}\\s*([\\s\\S]*?)(?=|$)`, 'i');
            const match = data.result.match(regex);

            if (match && match[1]) {
              // "-"
              const suggestions = match[1]
                .split('\n')
                .filter(line => line.trim().startsWith('-'))
                .map(line => line.trim().replace(/^-/, '').trim());

              this.feedbackCategories.push({
                title: category,
                suggestions: suggestions.length ? suggestions : ['']
              });
            }
          });
        }
      } catch (error) {
        console.error(':', error);
        this.feedbackCategories = [{
          title: '',
          suggestions: [
            '',
            '',
            ''
          ]
        }];
      }
    },

    // 
    renderRadarChart1() {
      // DOM
      this.$nextTick(() => {
        const ctx = this.$refs.radarChart?.getContext('2d');
        if (!ctx) {
          console.error('Canvas');
          return;
        }

        // 
        if (this.chart) {
          this.chart.destroy();
        }

        // 
        if (!this.reportIndicators || !this.reportValues || this.reportIndicators.length !== this.reportValues.length) {
          console.error('');
          return;
        }

        // 
        this.chart = new Chart(ctx, {
          type: 'radar',
          data: {
            labels: this.reportIndicators,
            datasets: [{
              label: '',
              data: this.reportValues,
              backgroundColor: 'rgba(59, 130, 246, 0.2)',
              borderColor: 'rgba(59, 130, 246, 1)',
              pointBackgroundColor: 'rgba(59, 130, 246, 1)',
              pointBorderColor: '#fff',
              pointHoverBackgroundColor: '#fff',
              pointHoverBorderColor: 'rgba(59, 130, 246, 1)'
            }]
          },
          options: {
            responsive: true,
            maintainAspectRatio: false,
            scales: {
              r: {
                angleLines: { display: true },
                suggestedMin: 0,
                suggestedMax: 100,
                ticks: { callback: (value) => `${value}` }
              }
            },
            plugins: {
              legend: { position: 'top' },
              tooltip: {
                callbacks: { label: (context) => `${context.label}: ${context.raw}` }
              }
            }
          }
        });
      });
    },

    // 
    getScoreLevel(score) {
      if (score >= 85) return '';
      if (score >= 75) return '';
      if (score >= 65) return '';
      return '';
    },

    // CSS
    getScoreClass(score) {
      if (score >= 85) return 'excellent';
      if (score >= 75) return 'good';
      if (score >= 65) return 'average';
      return 'poor';
    }
  }
};
</script>

<style scoped>
#dialog {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #EDEDF5;
}

.van-nav-bar {
  --van-nav-bar-background-color: #EFF1FD;
  --van-nav-bar-title-text-color: black;
}

.chat-container {
  width: 100%;
  overflow-y: auto;
  box-sizing: border-box;
}

.messages {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0 15px;
}

.message-item {
  display: flex;
  align-items: flex-start;
  margin: 5px 0;
}

.user-message {
  justify-content: flex-end;

}

/*  */
.user-message .message-bubble,
.user-message .voice-bubble,
.user-message .expression-bubble {
  margin-right: 8px;
  /*  */
  margin-left: 0;
  /*  */
}

.avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  flex-shrink: 0;
}

.ai-message .avatar {
  background-color: #e6f7ff;
  color: #1890ff;
}

.user-message .avatar {
  background-color: #f5f5f5;
  color: #8c8c8c;
}

.message-bubble {
  padding: 10px 15px;
  border-radius: 18px;
  max-width: 80%;
  word-wrap: break-word;
  position: relative;
}

.ai-message .message-bubble {
  background-color: #ffffff;
  border-top-left-radius: 4px;
}

.user-message .message-bubble {
  background-color: #1677ff;
  color: white;
  border-top-right-radius: 4px;
}

.voice-bubble {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 18px;
  position: relative;
}

.ai-message .voice-bubble {
  background-color: #ffffff;
  border-top-left-radius: 4px;
}

.user-message .voice-bubble {
  background-color: #1677ff;
  color: white;
  border-top-right-radius: 4px;
}

.voice-controls {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 150px;
}

.voice-wave {
  display: flex;
  align-items: center;
  gap: 3px;
  height: 24px;
  flex-grow: 1;
}

.voice-wave div {
  width: 3px;
  background-color: currentColor;
  border-radius: 3px;
  transition: height 0.2s ease;
}

.voice-duration {
  font-size: 12px;
  color: currentColor;
  opacity: 0.8;
}

.play-icon {
  cursor: pointer;
}

.voice-actions {
  display: flex;
  gap: 5px;
  margin-top: 8px;
  flex-wrap: wrap;
}

.voice-text-result {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px dashed #eee;
  font-size: 14px;
  color: #333;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px;
  border-radius: 4px;
}

.voice-speed-result {
  margin-top: 5px;
  font-size: 12px;
  color: #666;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 3px 5px;
  border-radius: 4px;
}

.voice-tone-result {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px dashed #eee;
  font-size: 12px;
  color: #666;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px;
  border-radius: 4px;
}

.voice-logic-result {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px dashed #eee;
  font-size: 13px;
  color: #333;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px;
  border-radius: 4px;
}

/*  */
.expression-bubble {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 18px;
  position: relative;
}

.ai-message .expression-bubble {
  background-color: #ffffff;
  border-top-left-radius: 4px;
}

.user-message .expression-bubble {
  background-color: #1677ff;
  color: white;
  border-top-right-radius: 4px;
}

.expression-image-container {
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 10px;
}

.expression-image {
  width: 200px;
  height: auto;
  display: block;
}

.expression-results,
.pose-result,
.comprehensive-analysis {
  margin-top: 8px;
  font-size: 13px;
  padding: 5px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.7);
  color: #333;
}

.expression-actions {
  display: flex;
  gap: 5px;
  margin-top: 8px;
  justify-content: flex-end;
}

.error-message {
  background-color: #fff2f0 !important;
  color: #f5222d !important;
}

.typing {
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

.typing span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #6b7280;
  animation: typing 1.4s infinite ease-in-out both;
}

.typing span:nth-child(1) {
  animation-delay: -0.32s;
}

.typing span:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes typing {

  0%,
  80%,
  100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}


#dialog_bottombar {
  width: 100%;
  background-color: #EFF1FD;
}

#dialog_bottombar_inside {
  position: relative;
  width: 100%;
  padding: 8px 2.5% 15px;
  box-sizing: border-box;
}

.recording {
  color: #f5222d !important;
  animation: pulse 1.5s infinite;
}

.camera-active {
  color: #00b42a !important;
  animation: pulse 2s infinite;
}

.report-icon {
  color: #722ed1 !important;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.recording-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  z-index: 1000;
}

.recording-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #f5222d;
  margin: 0 auto 10px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

.status-bar {
  position: absolute;
  bottom: 120px;
  left: 0;
  width: 100%;
  text-align: center;
  transition: opacity 0.3s ease;
}

/*  */
.camera-modal {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.camera-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #eee;
}

.camera-preview {
  flex-grow: 1;
  position: relative;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.camera-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.camera-loading,
.camera-error {
  position: absolute;
  color: white;
  text-align: center;
  padding: 20px;
}

.camera-controls {
  padding: 15px;
  display: flex;
  gap: 10px;
  justify-content: center;
  background-color: #f5f5f5;
}

/*  */
.report-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}

.report-header {
  text-align: center;
  padding: 10px 20px;
  position: relative;
  border-bottom: 1px solid #eee;
}

.report-header h2 {
  margin: 0;
  font-size: 18px;
  color: #1e293b;
}

.close-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
}

.report-loading,
.report-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  gap: 15px;
  padding: 20px;
}

.report-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 15px;
}

/*  */
.radar-chart-container {
  margin-bottom: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.chart-title {
  font-size: 16px;
  color: #334155;
  text-align: center;
  margin-bottom: 10px;
  font-weight: 500;
}

.chart-wrapper {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*  */
.ability-details-container {
  margin-bottom: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
}

.details-title {
  font-size: 16px;
  color: #334155;
  text-align: center;
  margin-bottom: 10px;
  font-weight: 500;
}

.details-list {
  display: grid;
